const SET_MOUNTED_COMP = 'SET_MOUNTED_COMP',
      SET_USER = 'SET_USER',
      ADD_TO_CART = 'ADD_TO_CART',
      REMOVE_FROM_CART = 'REMOVE_FROM_CART';

let initialState = {
  sessionUser:'',
  mountedComp:'',
  cart: []
};

export default function reducer(state = initialState, action){
  console.log(action.type);
  switch(action.type) {
    case REMOVE_FROM_CART:
      return Object.assign({}, state, {cart:state.cart.filter((e)=>{
        return action.id !== e.itemId;
      })})
    case ADD_TO_CART:
      return Object.assign({}, state, {cart:state.cart.concat({name: action.name, price: action.price, itemId:state.cart[state.cart.length-1]?state.cart[state.cart.length-1].itemId+1 : 1})});
    case SET_MOUNTED_COMP:
      return Object.assign({}, state, {mountedComp: action.val});
    case SET_USER:
      return Object.assign({}, state, {sessionUser: action.val})
    default:
      return Object.assign({}, state);
  }
}



